.titleForm {
  font-size: clamp(1rem, 2.5vw, 2rem); 
  margin:1rem;
}

.formContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: calc(83vh - 42px);
}

.checkoutForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 60vw;
  background-color: white;
  margin: 2.5rem;
  border-radius: 1rem;
  min-height: calc(55vh - 20px);
}

.inputGroup {
  position: relative;
  margin-top: 1rem;
 }
 
 .input {
  border: solid 1.5px #9e9e9e;
  border-radius: 1rem;
  background: none;
  padding: 1rem;
  font-size: 1rem;
  color: #000000;
  transition: border 150ms cubic-bezier(0.4,0,0.2,1);
  width:35vw;
 }
 
 .userLabel {
  position: absolute;
  left: 15px;
  color: #000000;
  pointer-events: none;
  transform: translateY(1rem);
  transition: 150ms cubic-bezier(0.4,0,0.2,1);
 }
 
 .input:focus, input:valid {
  outline: none;
  border: 1.5px solid #1a73e8;
 }
 
 .input:focus ~ label, input:valid ~ label {
  transform: translateY(-50%) scale(0.8);
  background-color: white;
  padding: 0 .2em;
  color: #2196f3;
 }

 .checkoutForm Button {
  width: 8vw;
  margin: 3rem;
  background-color: #AF1B3F;
}

.checkoutForm Button:hover {
  background-color:#a84a62;
}